/* animation pour faire clignoter la case du roi*/
#king{
  animation: clignoter 1s 3;
}

@keyframes clignoter {
  0% {
    background-color: #0dcaf0;
  }
  25% {
    background-color: #f7f7f7;
  }
  50% {
    background-color: #0dcaf0;
  }
  75% {
    background-color: #f7f7f7;
  }
  100% {
    background-color: #0dcaf0;
  }
}
	